"use strict";
import useCopyClipboard from "hooks/useCopyClipboard";
import { useCallback } from "react";
import { Copy } from "react-feather";
import { Flex, Text, useMedia, useSporeColors } from "ui/src";
import { shortenAddress } from "utilities/src/addresses";
export default function AddressCopy({ address }) {
  const colors = useSporeColors();
  const [isCopied, setCopied] = useCopyClipboard();
  const media = useMedia();
  const shouldEnableCopy = !media.md;
  const shouldShowActions = shouldEnableCopy && !isCopied;
  const copyCallBack = useCallback(() => {
    setCopied(address);
  }, [address, setCopied]);
  return <Flex row gap={"$gap8"} onPress={copyCallBack} cursor="pointer" alignItems="center">
      <Text variant="body4" color={colors.neutral2.val}>
        {shortenAddress(address)}
      </Text>
      <Copy
    data-testid="breadcrumb-hover-copy"
    size={13}
    color={shouldShowActions ? colors.neutral3.val : "transparent"}
  />
    </Flex>;
}
