"use strict";
import { useState } from "react";
import { Check, Clock, Lock } from "react-feather";
import styled from "styled-components";
import { Flex, Separator, Text, styled as UiStyled, useSporeColors } from "ui/src";
import { colors as BaseColor } from "ui/src/theme";
const ItemWrapper = UiStyled(Flex, {
  row: true,
  gap: "$gap8",
  alignItems: "center"
});
const IconWrapper = UiStyled(Flex, {
  width: 28,
  height: 28,
  borderRadius: "$roundedFull",
  backgroundColor: "$surface3",
  centered: true
});
const pulseKeyframe = `
  @keyframes pulse {
    0% {
      opacity: 1;
    }
    50% {
      opacity: 0.5;
    }
      100% {
      opacity: 1;
    }
  }`;
export default function RelayDepositReviewer() {
  const colors = useSporeColors();
  const [depositPending, setDepositPending] = useState(true);
  const listItems = [
    {
      icon: Lock,
      text: "Select the Lock you want to deposit",
      isActive: true
    },
    {
      icon: Lock,
      text: "Reset done for Lock #83065",
      isActive: false
    },
    {
      icon: Check,
      text: "Confirm unlock date change",
      isActive: true
    },
    {
      icon: Clock,
      text: "Waiting for next actions...",
      isActive: false
    }
  ];
  const styleProvider = (index) => {
    return depositPending && index === 3 ? {
      opacity: 1,
      animation: "pulse 1.5s linear infinite"
    } : {};
  };
  const Button = styled.button`
    all: unset;
    padding: 16px 10px;
    background-color: ${colors.accent1.val};
    color: ${colors.surface1.val};
    border-radius: 12px;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 0.2s ease;
    &:hover {
      opacity: 0.7;
    }
  `;
  return <Flex
    flexDirection="column"
    p={"$spacing16"}
    borderRadius={"$rounded12"}
    gap={"$gap36"}
    borderWidth={1}
    borderColor={"$surface3"}
    style={{
      background: `linear-gradient(180deg, ${colors.surface2.variable} 0%, transparent 100%)`
    }}
  >
      <style>{pulseKeyframe}</style>
      <Text variant="buttonLabel1">Relay Deposit</Text>

      <Flex flexDirection="column">
        {listItems.map((item, index) => <Flex>
            {index > 0 && <Separator left={14} vertical minHeight={22} width={2} borderColor={"$surface3"} />}

            <ItemWrapper key={item.text}>
              <IconWrapper>
                <item.icon
    style={styleProvider(index)}
    size={14}
    color={item.isActive ? BaseColor.greenVibrant : BaseColor.brownVibrant}
  />
              </IconWrapper>

              <Text variant="body3" style={styleProvider(index)} color={"$neutral2"}>
                {item.text}
              </Text>
            </ItemWrapper>
          </Flex>)}
      </Flex>

      <Button role="button">
        <Text variant="buttonLabel2" color={"inherit"}>
          Deposit
        </Text>
      </Button>
    </Flex>;
}
